<template>
  <button
  class="el-button"
  :class = "[
    type? 'el-button--' + type : '',
    {'is-disabled': buttonDisabled,
    'is-loading': loading}
  ]"
  :disabled="buttonDisabled">
    <i :v-if="buttonLoading" class="el-icon-loading"></i>
    <i :v-if="icon && !buttonLoading" :class="icon"></i>
    <slot></slot>
  </button>
</template>
<script>
export default {
  name: 'ElButton',
  props: {
    disabled: Boolean,
    type: {
      type: String,
      default: 'default'
    },
    loading: Boolean,
    icon: {
      type: String,
      default: ''
    }
  },
  computed: {
    buttonDisabled () {
      return this.disabled;
    },
    buttonLoading() {
      return this.loading;
    }
  }
}  
</script>
<style>

</style>



